body {
  background: url('../public/img/body-bg.jpg');
  background-size: 100vw 100vh;
  background-repeat: no-repeat;
}

.card-bg {
  background: transparent;
}

.icon-bg {
  background-color: #186a8a5f !important;
}

.weather-text {
  background-color: #08379594;
}

.weather-text:nth-child(n) {
  color: aliceblue;
}

.svg {
  background-color: #ADE4DB;
}

.loading-icon {
  background: #00FFCA;
}

@media screen and (max-width: 600px) {
  body {
    background-size: 200vw 100vh;
  }
}